<template>
  <div class="gain-page-content">
    <div class="header-content">
      <page-title :title="pageName"></page-title>
    </div>
    <div class="main-content">
      <div class="block-content-table">
        <min-title
          nameVal="毛利概览-店铺"
          :show="'3'"
        ></min-title>
        <table-list :tableData="mlglTableData"></table-list>
      </div>
      <div class="block-content-table">
        <min-title
          nameVal="毛利分析-店铺"
          :tabsData="blockSwitchList"
          :show="'1'"
          :type="'gain-mlfx'"
          @tabsType="tabsType"
        ></min-title>
        <table-list
          :tableData="mlfxTableData"
          :type="'gain-mlfx'"
          :needClick="false"
          :defaultSelected="true"
          @changeTableOrder="changeTableOrder"
          @changeTableRow="changeTableRow"
        ></table-list>
      </div>
      <div class="block-content-chart">
        <min-title
          :nameVal="qsfxTitle"
          :show="'3'"
        ></min-title>
        <div class="block-chart">
          <BarLineChart
            :chart-data="qsfxdpLineData"
            :option="qsfxdpLineOption(qsfxdpLineData)"
          ></BarLineChart>
        </div>
      </div>
      <div class="block-content-chart">
        <min-title
          :nameVal="ystbfxTitle"
          :tabsData="blockSwitchList"
          :show="'1'"
          :type="'gain-ystbfx'"
          @tabsType="tabsType"
        ></min-title>
        <div class="block-chart">
          <BarLineChart
            :chart-data="ystbfxLineData"
            :option="ystbfxLineOption(ystbfxLineData)"
          ></BarLineChart>
        </div>
      </div>
      <div class="block-content-chart">
        <min-title
          :nameVal="ysqsfxTitle"
          :tabsData="blockSwitchList_Ysfx"
          :show="'1'"
          :type="'gain-ysqsfx'"
          @tabsType="tabsType"
        ></min-title>
        <div class="block-chart">
          <BarLineChart
            :chart-data="ysqsfxdpLineData"
            :option="ysqsfxdpLineOption(ysqsfxdpLineData)"
          ></BarLineChart>
        </div>
      </div>
      <div class="block-content-table">
        <min-title
          :nameVal="jgfxTitle"
          :show="'3'"
        ></min-title>
        <type-list
          :tabsData="jgfxTypeList"
          :switchType="'gain-jgfx-type'"
          @activeTypeName=changeJgfxType
        ></type-list>
        <div class="switch-btn">
          <div class="switch-btn-core">
            <tabs :tabsData="blockSwitchList" @tabsType="tabsType" :switchType="'gain-jgfx-up'"></tabs>
          </div>
        </div>
        <table-list
          :tableData="jgfxdpTableData"
          :type="'gain-jgfx'"
          @changeTableOrder="changeTableOrder"
        ></table-list>
      </div>
    </div>
  </div>
</template>

<script>
import pageTitle from '../../components/common/pageTitle'
import MinTitle from '../../components/common/minTitle'
import BarLineChart from '../../components/echarts/BarLineChart'
import TableList from '../../components/common/tableList'
import TypeList from '../../components/common/typeList'
// import SwitchBtnList from '../../components/common/switchBtnList'
import Tabs from '../../components/common/tabs'
import {px2px} from '../../assets/js/lib/common/flexible'
import _ from 'lodash'
import {initNum, compareSort, formatNumber, switchPercent} from '../../assets/js/core/weapon' // 引入排序方法
import apiService from '../../assets/js/api'
export default {
  name: '',
  data () {
    return {
      pageName: '扣前毛利(万元)',
      mlglTableData: {
        tableHeader: [{name: '时间', prop: 'sj', needOrder: false, order: '0', isPresent: false, needArrow: false},
          {name: '收入', prop: 'sr', needOrder: false, order: '1', isPresent: false, needArrow: false},
          {name: '毛利', prop: 'ml', needOrder: false, order: '0', isPresent: false, needArrow: false},
          {name: '毛利率', prop: 'mll', needOrder: false, order: '0', isPresent: true, needArrow: false},
          {name: '差异', prop: 'tb', needOrder: false, order: '0', isPresent: true, needArrow: true}
        ],
        tableContent: []
      },
      blockSwitchList: [{name: '月', value: 'M'}, {name: '季', value: 'J'}, {name: '年累', value: 'NL'}],
      blockSwitchList_Ysfx: [{name: '收入', value: '收入'}, {name: '成本', value: '成本'}],
      mlfxTableData: {
        tableHeader: [
          {name: '店铺', prop: 'dp', needOrder: false, order: '0', isPresent: false, needArrow: false},
          {name: '毛利率', prop: 'mll', needOrder: true, order: '2', isPresent: true, needArrow: false},
          {name: '毛利率排名', prop: 'mllpm', needOrder: true, order: '0', isPresent: false, needArrow: false},
          {name: '毛利率差异', prop: 'mllcy', needOrder: true, order: '0', isPresent: true, needArrow: true},
          {name: '差异排名', prop: 'cypm', needOrder: true, order: '0', isPresent: false, needArrow: false}
        ],
        tableContent: []
      },
      qsfxdpLineData: [],
      ystbfxLineData: [],
      ysqsfxdpLineData: [],
      qsfxTitle: '趋势分析',
      ystbfxTitle: '因素同比分析',
      ysqsfxTitle: '因素趋势分析',
      jgfxTitle: '结构分析',
      jgfxIndex: 0,
      mlfxDpIndex: 0,
      ystbfxDpIndex: 0,
      ysqsfxDpTypeIndex: 0,
      jgfxdpTypeIndex: 0,
      jgfxdpIndex: 0,
      mlfxDpTableDataInit: [],
      qsfxDpLineDataInit: [],
      ystbfxDpBarLineDataInit: [],
      ysqsfxDpLineDataInit: [],
      jgfxDpTableDataInit: [],
      zgsName: '南京子公司',
      jgfxTypeList: [{name: '大类', value: '大类'}, {name: 'SPU', value: 'SPU'}],
      jgfxdpTableData: {
        tableHeader: [
          {name: '大类', prop: 'dl', needOrder: false, order: '0', isPresent: false, needArrow: false},
          {name: '收入同比', prop: 'srtb', needOrder: true, order: '2', isPresent: true, needArrow: false, newLine: true},
          {name: '成本同比', prop: 'cbtb', needOrder: true, order: '0', isPresent: true, needArrow: true, newLine: true},
          {name: '毛利率', prop: 'mll', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
          {name: '同期', prop: 'tq', needOrder: true, order: '0', isPresent: true, needArrow: false},
          {name: '差异', prop: 'cy', needOrder: true, order: '0', isPresent: true, needArrow: true},
          {name: '占比', prop: 'zb', needOrder: true, order: '0', isPresent: true, needArrow: false}
        ],
        tableContent: []
      }
    }
  },
  computed: {
    dateStr () {
      return this.$store.state.dateStr
    },
    dateStrM () {
      return this.$store.getters.dateStrM
    },
    dateStrJ () {
      return this.$store.getters.dateStrJ
    },
    userIdentityInit () {
      return this.$store.state.userIdentityInit
    },
    selectedDwName () {
      const authList = this.$store.state.authList
      let str = ''
      if (this.userIdentityInit === '2') {
        for (let item of authList) {
          if (item.shop === this.selectedDpName) {
            str = item.role
            break
          }
        }
      } else if (this.userIdentityInit === '3') {
        for (let item of authList) {
          if (item.role === this.selectedDpName) {
            str = item.zgs
            break
          }
        }
      }
      return str
    },
    selectedDpName () {
      return this.$store.state.selectedDpName
    }
  },
  methods: {
    tabsType ({activeValue, name, type, index}) {
      if (type === 'gain-mlfx') {
        this.mlfxDpIndex = index
        this.dealDpMlfxTableData()
      } else if (type === 'gain-ystbfx') {
        this.ystbfxDpIndex = index
        this.dealDpYstbfxBarLineData()
      } else if (type === 'gain-ysqsfx') {
        this.ysqsfxDpTypeIndex = index
        this.dealDpYsqsfxBarLineData()
      } else if (type === 'gain-jgfx-up') {
        this.jgfxdpIndex = index
        this.dealJgfxDpTableData()
      }
      // alert('你点击了' + activeValue + name + type)
    },
    changeTableOrder ({header, type}) {
      let tempTable = {tableHeader: [], tableContent: []}
      if (type === 'gain-mlfx') {
        tempTable = _.cloneDeep(this.mlfxTableData)
      } else if (type === 'gain-jgfx') {
        tempTable = _.cloneDeep(this.jgfxdpTableData)
      }
      const headerList = []
      let tableContent = []
      for (let item of tempTable.tableHeader) {
        if (header.order === '0') {
          // 说明想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '1') {
          // 说明之前是升序，想要降序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '2'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '2') {
          // 说明之前是降序，想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        }
        headerList.push(item)
      }
      if (header.order === '0') {
        // 说明想要升序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, true))
      } else if (header.order === '1') {
        // 说明之前是升序，想要降序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, false))
      } else if (header.order === '2') {
        // 说明之前是降序，想要升序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, true))
      }
      if (type === 'gain-mlfx') {
        this.mlfxTableData.tableHeader = headerList
        this.mlfxTableData.tableContent = tableContent
      } else if (type === 'gain-jgfx') {
        this.jgfxdpTableData.tableHeader = headerList
        this.jgfxdpTableData.tableContent = tableContent
      }
    },
    changeTableRow ({row, name, type}) {
      // 当点击了表格的某一行，进行联动操作
      this.qsfxTitle = '趋势分析-' + name
      this.ystbfxTitle = '因素同比分析-' + name
      this.ysqsfxTitle = '因素趋势分析-' + name
      this.getDpYsqsfxBarLineData(name)
      this.jgfxTitle = '结构分析-' + name
    },
    changeJgfxType ({ activeValue, name, type, index }) {
      if (type === 'gain-jgfx-type') {
        this.jgfxdpTypeIndex = index
        this.dealJgfxDpTableData()
      }
    },
    changeJgfxSubType ({btn, index}) {
    },
    qsfxdpLineOption (dataArr) {
      const option = {
        legend: {
          show: true,
          textStyle: {
            fontFamily: 'PingFangSC-Regular',
            color: '#71768C',
            fontSize: px2px(24)
          }
        },
        xAxis: [
          {
            type: 'category',
            name: '',
            interval: 0,
            nameLocation: 'start',
            nameTextStyle: {
              color: '#71768C',
              fontSize: px2px(24),
              padding: [0, 0, px2px(-30), px2px(-80)]
            },
            data: [],
            axisPointer: {
              type: 'shadow'
            },
            axisLabel: {
              show: true,
              interval: 0,
              textStyle: {
                color: '#71768C'
              },
              fontSize: px2px(24)
            },
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '',
            nameTextStyle: {
              color: '#71768C',
              fontSize: px2px(24)
            },
            axisLabel: {
              show: true,
              fontSize: px2px(24),
              textStyle: {
                color: '#71768C'
              },
              formatter: '{value}%'
            },
            axisLine: {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            splitLine: {
              show: false,
              lineStyle: {
                color: '#7d838b'
              }
            },
            axisTick: {
              show: false
            }
          },
          {
            type: 'value',
            name: '',
            nameTextStyle: {
              color: '#71768C',
              fontSize: px2px(24)
            },
            show: true,
            axisLabel: {
              show: true,
              fontSize: px2px(24),
              textStyle: {
                color: '#71768C'
              },
              formatter: '{value}%'
            },
            axisLine: {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            splitLine: {
              show: false,
              lineStyle: {
                color: '#213A6B'
              }
            },
            axisTick: {
              show: false
            }
          }
        ],
        series: [
          {
            type: 'bar',
            name: '毛利率差异',
            barWidth: px2px(20),
            itemStyle: {
              color: '#34C9F8 '
            },
            data: []
          },
          {
            type: 'line',
            name: '毛利率',
            symbol: 'none',
            smooth: true,
            yAxisIndex: 1,
            itemStyle: {
              color: '#0000FF'
            },
            lineStyle: {
              color: '#0000FF',
              type: 'solid',
              width: px2px(4)
            },
            data: []
          },
          {
            type: 'line',
            name: '同期毛利率',
            yAxisIndex: 1,
            smooth: true,
            symbol: 'none',
            itemStyle: {
              color: '#FFD700'
            },
            lineStyle: {
              color: '#FFD700',
              type: 'solid',
              width: px2px(4)
            },
            data: []
          }
        ]
      }
      const labelList = []
      const mllcyList = []
      const mllList = []
      const tqmllList = []
      for (let item of this.qsfxdpLineData) {
        labelList.push(item.label)
        mllcyList.push(item.mllcy)
        mllList.push(item.mll)
        tqmllList.push(item.tqmll)
      }
      option.xAxis[0].data = labelList
      for (let i = 0; i < mllcyList.length; i++) {
        option.series[0].data[i] = {value: mllcyList[i], itemStyle: {color: parseFloat(mllcyList[i]) > 0 ? '#34C9F8' : '#FF0000'}}
      }
      option.series[1].data = mllList
      option.series[2].data = tqmllList
      return option
    },
    ystbfxLineOption (dataArr) {
      const labelList = []
      const valueList = []
      for (let item of this.ystbfxLineData) {
        labelList.push(item.label)
        valueList.push(item.value)
      }
      const option = {
        legend: {
          show: true,
          textStyle: {
            fontFamily: 'PingFangSC-Regular',
            color: '#71768C',
            fontSize: px2px(24)
          }
        },
        xAxis: [
          {
            type: 'category',
            name: '',
            interval: 0,
            nameLocation: 'start',
            nameTextStyle: {
              color: '#71768C',
              fontSize: px2px(24),
              padding: [0, 0, px2px(-30), px2px(-80)]
            },
            data: [],
            axisPointer: {
              type: 'shadow'
            },
            axisLabel: {
              show: true,
              interval: 0,
              textStyle: {
                color: '#71768C'
              },
              fontSize: px2px(24)
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: '#98FB98'
              }
            },
            axisTick: {
              show: false
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '',
            nameTextStyle: {
              color: '#71768C',
              fontSize: px2px(24)
            },
            position: 'right',
            axisLabel: {
              show: true,
              fontSize: px2px(24),
              textStyle: {
                color: '#71768C'
              },
              formatter: '{value}%'
            },
            axisLine: {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            splitLine: {
              show: false,
              lineStyle: {
                color: '#7d838b'
              }
            },
            axisTick: {
              show: false
            }
          }
        ],
        series: [
          {
            type: 'bar',
            name: '收入同比',
            smooth: true,
            barWidth: px2px(80),
            symbol: 'none',
            yAxisIndex: 0,
            label: {
              show: true,
              position: 'top',
              fontFamily: 'PingFangSC-Regular',
              color: '#71768C',
              fontSize: px2px(24),
              formatter: function (param) {
                // console.info(initNum(param.value) === 0)
                if (initNum(param.value) === 0) {
                  return ''
                } else {
                  return param.value + '%'
                }
              }
            },
            barGap: '-100%',
            itemStyle: {
              color: '#FFAB00'
            },
            data: []
          },
          {
            type: 'bar',
            name: '成本同比',
            smooth: true,
            label: {
              show: true,
              position: 'top',
              fontFamily: 'PingFangSC-Regular',
              color: '#71768C',
              fontSize: px2px(24),
              formatter: function (param) {
                if (initNum(param.value) === 0) {
                  return ''
                } else {
                  return param.value + '%'
                }
              }
            },
            barWidth: px2px(80),
            symbol: 'none',
            yAxisIndex: 0,
            itemStyle: {
              color: '#0000FF'
            },
            data: []
          },
          {
            type: 'bar',
            name: '毛利率差异',
            smooth: true,
            label: {
              show: true,
              position: 'top',
              fontFamily: 'PingFangSC-Regular',
              color: '#71768C',
              fontSize: px2px(24),
              formatter: function (param) {
                if (initNum(param.value) === 0) {
                  return ''
                } else {
                  return param.value + '%'
                }
              }
            },
            barWidth: px2px(80),
            symbol: 'none',
            yAxisIndex: 0,
            itemStyle: {
              color: '#87CEFA'
            },
            data: []
          }
        ]
      }

      option.xAxis[0].data = labelList
      option.series[0].data[0] = {value: valueList[0], itemStyle: {barBorderRadius: parseFloat(valueList[0]) > 0 ? [px2px(40), px2px(40), 0, 0] : [0, 0, px2px(40), px2px(40)]}}
      option.series[0].data[1] = {value: valueList[1], itemStyle: {barBorderRadius: parseFloat(valueList[1]) > 0 ? [px2px(40), px2px(40), 0, 0] : [0, 0, px2px(40), px2px(40)], color: '#0000FF'}}
      option.series[0].data[2] = {value: valueList[2], itemStyle: {barBorderRadius: parseFloat(valueList[2]) > 0 ? [px2px(40), px2px(40), 0, 0] : [0, 0, px2px(40), px2px(40)], color: '#87CEFA'}}
      return option
    },
    ysqsfxdpLineOption (dataArr) {
      const option = {
        color: ['#326DFF', '#FFBE14', '#FF0200'],
        'legend': {
          show: true,
          selectedMode: false,
          textStyle: {
            fontFamily: 'PingFangSC-Regular',
            color: '#71768C',
            fontSize: px2px(24)
          }
        },
        'xAxis': [
          {
            'type': 'category',
            'name': '',
            interval: 0,
            nameLocation: 'start',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24),
              padding: [0, 0, px2px(-30), px2px(-80)]
            },
            'data': [],
            'axisPointer': {
              'type': 'shadow'
            },
            'axisLabel': {
              'show': true,
              interval: 0,
              'textStyle': {
                'color': '#71768C'
              },
              fontSize: px2px(24)
            },
            'axisLine': {
              'show': false
            },
            axisTick: {
              'show': false
            }
          }
        ],
        'yAxis': [
          {
            'type': 'value',
            'name': '(万元)',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24)
            },
            'axisLabel': {
              'show': true,
              fontSize: px2px(24),
              'textStyle': {
                'color': '#71768C'
              }
            },
            'axisLine': {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            'splitLine': {
              show: false,
              'lineStyle': {
                'color': '#7d838b'
              }
            },
            axisTick: {
              'show': false
            }
          },
          {
            'type': 'value',
            'name': '同比',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24)
            },
            'show': true,
            'axisLabel': {
              show: true,
              fontSize: px2px(24),
              'textStyle': {
                'color': '#71768C'
              },
              formatter: '{value}%'
            },
            'axisLine': {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            'splitLine': {
              show: true,
              lineStyle: {
                color: '#213A6B'
              }
            }
          }
        ],
        series: [
          {
            type: 'bar',
            name: (this.ysqsfxDpTypeIndex === 0 ? '收入' : '成本'),
            barWidth: px2px(40),
            itemStyle: {
            },
            data: []
          },
          {
            type: 'line',
            name: (this.ysqsfxDpTypeIndex === 0 ? '收入同比' : '成本同比'),
            smooth: true,
            symbol: 'none',
            yAxisIndex: 1,
            lineStyle: {
              // color: '#FFBE14',
              type: 'solid',
              width: px2px(4)
            },
            data: []
          },
          {
            type: 'line',
            name: '同比0%',
            smooth: true,
            symbol: 'none',
            yAxisIndex: 1,
            lineStyle: {
              // color: '#FF0200',
              type: 'dashed',
              width: px2px(4)
            },
            data: []
          }]
      }
      const labelList = []
      const firstList = []
      const secondList = []
      const thirdList = []
      for (let item of this.ysqsfxdpLineData) {
        labelList.push(item.label)
        firstList.push(item.sr)
        secondList.push(item.srtb)
        thirdList.push(item.tb)
      }
      option.xAxis[0].data = labelList
      option.series[0].data = firstList
      option.series[1].data = secondList
      option.series[2].data = thirdList
      return option
    },
    getAllData () {
      this.getDpMlfxTableData(this.selectedDwName)
      this.getDpMlglTableData(this.selectedDpName)
      this.getDpQsfxBarLineData(this.selectedDpName)
      this.getDpYstbfxBarLineData(this.selectedDpName)
      this.getDpYsqsfxBarLineData(this.selectedDpName)
      this.getJgfxDpTableData(this.selectedDpName)
      this.qsfxTitle = '趋势分析-' + this.selectedDpName
      this.ystbfxTitle = '因素同比分析-' + this.selectedDpName
      this.jgfxTitle = '结构分析-' + this.selectedDpName
      this.ysqsfxTitle = '因素趋势分析-' + this.selectedDpName
    },
    async getDpMlglTableData (dpName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.gainShop.getDpMlglTableData,
        {dateStrD: this.dateStr, dateStrM: this.dateStrM, cusName: dpName, subName: this.selectedDwName})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        const list = []
        for (let item of result.res) {
          const obj = {}
          obj.sj = item.period_wid
          obj.sr = formatNumber(initNum(item.income_amt), 2)
          obj.ml = formatNumber(initNum(item.margin_amt), 2)
          obj.mll = switchPercent(item.margin_rate) // (initNum(item.margin_rate) * 100).toFixed(1)
          obj.tb = (initNum(item.margin_yoy) * 100).toFixed(1)
          list.push(obj)
        }
        this.mlglTableData.tableContent = list
      }
    },
    async getDpMlfxTableData (companyName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.gainShop.getDpMlfxTableData, {dateStrM: this.dateStrM, subName: companyName})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.mlfxDpTableDataInit = result
        this.dealDpMlfxTableData()
      }
    },
    dealDpMlfxTableData () {
      const list = []
      let dataList = []
      if (this.mlfxDpIndex === 0) {
        dataList = this.mlfxDpTableDataInit.resM || []
      } else if (this.mlfxDpIndex === 1) {
        dataList = this.mlfxDpTableDataInit.resJ || []
      } else if (this.mlfxDpIndex === 2) {
        dataList = this.mlfxDpTableDataInit.resNl || []
      }
      for (let item of dataList) {
        const obj = {}
        obj.dp = item.customer_name
        obj.mll = switchPercent(item.margin_rate)
        obj.mllpm = item.order_margin_rate
        obj.mllcy = switchPercent(item.deviation)
        obj.cypm = item.order_deviation
        list.push(obj)
      }
      this.mlfxTableData = {
        tableHeader: [
          {name: '店铺', prop: 'dp', needOrder: false, order: '0', isPresent: false, needArrow: false},
          {name: '毛利率', prop: 'mll', needOrder: true, order: '2', isPresent: true, needArrow: false},
          {name: '毛利率排名', prop: 'mllpm', needOrder: true, order: '0', isPresent: false, needArrow: false},
          {name: '毛利率差异', prop: 'mllcy', needOrder: true, order: '0', isPresent: true, needArrow: true},
          {name: '差异排名', prop: 'cypm', needOrder: true, order: '0', isPresent: false, needArrow: false}
        ],
        tableContent: list.sort(compareSort('mll', false))
      }
      // if (this.mlfxTableData.tableContent.length > 0) {
      //   this.qsfxTitle = '趋势分析-' + this.mlfxTableData.tableContent[0].dp
      //   this.ystbfxTitle = '因素同比分析-' + this.mlfxTableData.tableContent[0].dp
      //   this.jgfxTitle = '结构分析-' + this.mlfxTableData.tableContent[0].dp
      //   this.ysqsfxTitle = '因素趋势分析-' + this.mlfxTableData.tableContent[0].dp
      //   // this.$eventBus.$emit('resetSelectedRow', {type: 'gain-mlfx', name: this.mlfxTableData.tableContent[0].dp})
      //   // this.getDpMlglTableData(this.mlfxTableData.tableContent[0].dp)
      //   // this.getDpQsfxBarLineData(this.mlfxTableData.tableContent[0].dp)
      //   // this.getDpYstbfxBarLineData(this.mlfxTableData.tableContent[0].dp)
      //   // this.getDpYsqsfxBarLineData(this.mlfxTableData.tableContent[0].dp)
      //   // this.getJgfxDpTableData(this.mlfxTableData.tableContent[0].dp)
      //   // this.getQsfxBarLineData(this.mlfxTableData.tableContent[0].dp)
      //   // this.getYstbfxBarLineData(this.mlfxTableData.tableContent[0].dp)
      //   // this.getPmfxTableData(this.mlfxTableData.tableContent[0].dp)
      // } else {
      //   this.qsfxTitle = '趋势分析'
      //   this.ystbfxTitle = '因素同比分析'
      //   this.jgfxTitle = '结构分析'
      //   this.ysqsfxTitle = '因素趋势分析'
      //   this.mlglTableData.tableContent = []
      //   this.qsfxdpLineData = []
      //   this.ystbfxLineData = []
      // }
    },
    async getDpQsfxBarLineData (storeName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.gainCompany.getDpqsfxBarLineData, {cusName: storeName, dateStrM: this.dateStrM})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.qsfxDpLineDataInit = result.res
        this.dealDpQsfxBarLineData()
      }
    },
    dealDpQsfxBarLineData () {
      const list = []
      let dataList = []
      dataList = this.qsfxDpLineDataInit || []
      for (let item of dataList) {
        const obj = {}
        obj.label = item.period_wid
        obj.mll = (initNum(item.margin_rate) * 100).toFixed(1)
        obj.tqmll = (initNum(item.margin_yoy) * 100).toFixed(1)
        obj.mllcy = (initNum(item.deviation) * 100).toFixed(1)
        list.push(obj)
      }
      this.qsfxdpLineData = list
    },
    async getDpYstbfxBarLineData (storeName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.gainCompany.getDpYstbfxBarLineData, {cusName: storeName, dateStrM: this.dateStrM})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.ystbfxDpBarLineDataInit = result
        this.dealDpYstbfxBarLineData()
      }
    },
    dealDpYstbfxBarLineData () {
      const list = []
      let dataList = []
      if (this.ystbfxDpIndex === 0) {
        dataList = this.ystbfxDpBarLineDataInit.resM || []
      } else if (this.ystbfxDpIndex === 1) {
        dataList = this.ystbfxDpBarLineDataInit.resJ || []
      } else if (this.ystbfxDpIndex === 2) {
        dataList = this.ystbfxDpBarLineDataInit.resNl || []
      }
      if (dataList.length > 0) {
        const item = dataList[0]
        const obj1 = {}
        const obj2 = {}
        const obj3 = {}
        obj1.label = '收入'
        obj1.value = (initNum(item.income_yoy) * 100).toFixed(1)
        obj2.label = '成本'
        obj2.value = (initNum(item.cost_yoy) * 100).toFixed(1)
        obj3.label = '毛利率差异'
        obj3.value = (initNum(item.deviation) * 100).toFixed(1)
        list[0] = obj1
        list[1] = obj2
        list[2] = obj3
        this.ystbfxLineData = list
      } else {
        this.ystbfxLineData = []
      }
    },
    async getDpYsqsfxBarLineData (storeName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.gainCompany.getDpYsqsfxBarLineData, {cusName: storeName, dateStrM: this.dateStrM})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.ysqsfxDpLineDataInit = result.res
        this.dealDpYsqsfxBarLineData()
      }
    },
    dealDpYsqsfxBarLineData () {
      const list = []
      let dataList = []
      dataList = this.ysqsfxDpLineDataInit || []
      for (let item of dataList) {
        const obj = {}
        obj.label = item.period_wid
        obj.tb = (initNum(item.target_yoy) * 100).toFixed(1)
        if (this.ysqsfxDpTypeIndex === 0) {
          obj.sr = (initNum(item.income_amt)).toFixed(1)
          obj.srtb = (initNum(item.income_yoy) * 100).toFixed(1)
        } else if (this.ysqsfxDpTypeIndex === 1) {
          obj.sr = (initNum(item.cost_amt)).toFixed(1)
          obj.srtb = (initNum(item.cost_yoy) * 100).toFixed(1)
        }
        list.push(obj)
      }
      this.ysqsfxdpLineData = list
    },
    async getJgfxDpTableData (storeName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.gainCompany.getDpjgfxTableData, {cusName: storeName, dateStrM: this.dateStrM})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.jgfxDpTableDataInit = result
        this.dealJgfxDpTableData()
      }
    },
    dealJgfxDpTableData () {
      const list = []
      let dataList = []
      if (this.jgfxdpTypeIndex === 0) {
        if (this.jgfxdpIndex === 0) {
          dataList = this.jgfxDpTableDataInit.resDM || []
        } else if (this.jgfxdpIndex === 1) {
          dataList = this.jgfxDpTableDataInit.resDJ || []
        } else if (this.jgfxdpIndex === 2) {
          dataList = this.jgfxDpTableDataInit.resDNl || []
        }
      } else if (this.jgfxdpTypeIndex === 1) {
        if (this.jgfxdpIndex === 0) {
          dataList = this.jgfxDpTableDataInit.resSpuM || []
        } else if (this.jgfxdpIndex === 1) {
          dataList = this.jgfxDpTableDataInit.resSpuJ || []
        } else if (this.jgfxdpIndex === 2) {
          dataList = this.jgfxDpTableDataInit.resSpuNl || []
        }
      }
      for (let item of dataList) {
        const obj = {}
        obj.dl = item.type_name
        obj.srtb = switchPercent(item.income_yoy)
        obj.cbtb = switchPercent(item.cost_yoy)
        obj.mll = switchPercent(item.margin_rate)
        obj.tq = switchPercent(item.margin_yoy)
        obj.cy = switchPercent(item.deviation)
        obj.zb = switchPercent(item.rotio_rate)
        list.push(obj)
      }
      this.jgfxdpTableData = {
        tableHeader: [
          {name: '大类', prop: 'dl', needOrder: false, order: '0', isPresent: false, needArrow: false},
          {name: '收入同比', prop: 'srtb', needOrder: true, order: '2', isPresent: true, needArrow: false, newLine: true},
          {name: '成本同比', prop: 'cbtb', needOrder: true, order: '0', isPresent: true, needArrow: true, newLine: true},
          {name: '毛利率', prop: 'mll', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
          {name: '同期', prop: 'tq', needOrder: true, order: '0', isPresent: true, needArrow: false},
          {name: '差异', prop: 'cy', needOrder: true, order: '0', isPresent: true, needArrow: true},
          {name: '占比', prop: 'zb', needOrder: true, order: '0', isPresent: true, needArrow: false}
        ],
        tableContent: list.sort(compareSort('srtb', false))
      }
    }
  },
  created () {
    this.$store.commit('updateHeaderTitle', '扣前毛利')
    this.$store.commit('updateShowIcon', true)
    this.$store.commit('updateDateStr', this.$store.state.dateStrInit)
    this.$store.dispatch('updateUserOption', {USER_ACCOUNT: window.userAccount, EXAMINATION_SUBJECT: '直营', LEVEL: '店铺', URL: '毛利'})
    if (this.dateStr !== '') {
      this.getAllData()
    }
  },
  mounted () {
    this.$eventBus.$on('userSelected', this.getAllData)
  },
  beforeDestroy () {
    this.$eventBus.$off('userSelected', this.getAllData)
  },
  /**
  watch: {
    dateStr: {
      handler () {
        this.getAllData()
      }
    },
    selectedDwName: {
      handler () {
        this.getDpMlfxTableData(this.selectedDwName)
      }
    },
    selectedDpName: {
      handler () {
        this.getDpMlglTableData(this.selectedDpName)
        this.getDpQsfxBarLineData(this.selectedDpName)
        this.getDpYstbfxBarLineData(this.selectedDpName)
        this.getDpYsqsfxBarLineData(this.selectedDpName)
        this.getJgfxDpTableData(this.selectedDpName)
        this.qsfxTitle = '趋势分析-' + this.selectedDpName
        this.ystbfxTitle = '因素同比分析-' + this.selectedDpName
        this.jgfxTitle = '结构分析-' + this.selectedDpName
        this.ysqsfxTitle = '因素趋势分析-' + this.selectedDpName
      }
    }
  },
   **/
  components: {
    pageTitle,
    MinTitle,
    BarLineChart,
    TableList,
    TypeList,
    // SwitchBtnList,
    Tabs
  }
}
</script>

